import React, {Component} from 'react'
import {NavBar,WingBlank,TextareaItem,InputItem,WhiteSpace,Button} from  'antd-mobile'
import  Logo from '../../components/logo/logo'
import {connect} from 'react-redux'
import {updateUser} from '../../redux/acitons'
import ListItem from 'antd-mobile/lib/list/ListItem'
import {Redirect} from 'react-router-dom'
import  '../../assets/css/inde.less'
import HeaderSelector from '../../components/header-selector/header-selector'

class BossInfo extends Component {
    state = {
        header: '', // 头像名称
        info: '', // 职位简介
        post: '', // 职位名称
        company: '', // 公司名称
        salary: '' // 工资
        }
    //提供给header-selector的回调函数（传给子组件props），以用于改变状态
    setHeader = (val) =>{
        this.setState({
            header:val
        })
    }

    handleChange =(type,val) => {
        this.setState({
            [type]:val
        })
    }
    save = () =>{
      this.props.updateUser(this.state)  
    }
  render() {
          // 如果信息已经完善, 自动重定向到对应主界面
    const {header, type} = this.props.user
    if(header) { // 说明信息已经完善
      const path = type==='qiuzhizhe' ? '/worker' : '/boss'
      return <Redirect to={path}/>
    }
    return (
      <div>
          <NavBar>老板信息完善</NavBar>
          <HeaderSelector setHeader={this.setHeader}/>
          <InputItem placeholder='请输入职位' onChange={value => {this.handleChange('post',value)}}>招聘职位：</InputItem>
          <InputItem placeholder='请输入公司名称' onChange={value => {this.handleChange('company',value)}}>公司名称：</InputItem>
          <InputItem placeholder='请输入薪资' onChange={value => {this.handleChange('salary',value)}}>职位薪资：</InputItem> 
          <TextareaItem title='职位要求' rows={3} onChange={value => {this.handleChange('info',value)}}></TextareaItem>
          <Button type='primary' onClick={this.save}>保&nbsp;&nbsp;&nbsp;存</Button>
     </div>
    )
  }
}

export default connect(state =>({user:state.user}),
    {updateUser}
)(BossInfo)


