//包含若干个工具函数
import io from 'socket.io-client';
import {receive_msg}  from '../redux/acitons'

export function getRedirectTo(type, header) {
    let path
    // type
    if(type==='laoban') {
      path = '/boss'
    } else {
      path = '/worker'
    }
    // header
    if(!header) { // 没有值, 返回信息完善界面的path
      path += 'info'
    }
  
    return path
  }


//建立socket.io连接
  export function intiIO(dispatch,userid){
    if(!io.socket){    //单例对象，避免服务器反复连接
       io.socket = io('ws://localhost:4000') //连接服务器
       io.socket.emit('setname',userid)
       io.socket.on('receiveMsg',function(chatMsg){ //绑定监听
        console.log('接受服务器端：',chatMsg)
        if(userid === chatMsg.from || userid ===chatMsg.to){
          dispatch(receive_msg(chatMsg,userid))
        }
      })
    }
  }





