import {reqRegister,reqLogin,reqUpdateUser,reqUser,reqUserList,reqChatMsgList,reqReadMsg} from '../api'
import {AUTH_SUCCESS,ERROR_MSG,RECIEVE_USER,RESET_USER,RECIEVE_USERLIST,RECIEVE_MSG_LIST,RECIEVE_MSG,READ_MSG}  from './aciton-types'
import {intiIO} from '../utils/index'
import io from 'socket.io-client';

const auth_success = (user) =>({
    type:AUTH_SUCCESS,
    data:user
})

const error_msg = (msg) =>({
    type:ERROR_MSG,
    data:msg
})
const receive_user = (user) =>({
    type:RECIEVE_USER,
    data:user
})
export const reset_user = (msg) =>({
    type:RESET_USER,
    data:msg
})
const receive_userlist = (users) =>({
    type:RECIEVE_USERLIST,
    data:users
})
const receive_msg_list = ({users,chatMsgs,userid}) =>({
    type:RECIEVE_MSG_LIST,
    data:{users,chatMsgs,userid}
})
export const receive_msg=(chatMsg,userid) =>({
    type:RECIEVE_MSG,
    data:{chatMsg,userid}
})
const read_msg =(count,from,to) =>({
    type:READ_MSG,
    data:{count,from,to}
})

export const register =(user) =>{
    const {username,password,password2,type} = user
    if(password !== password2) 
        return error_msg('两次密码不一致')  //表单前端验证
    if(!username)
        return error_msg('用户名不能为空')  //表单前端验证

    //前端验证完毕，与后端ajax通信，发布异步aciton
    return async dispatch =>{
        const response =await reqRegister({username,password,type})
        const result = response.data
        if(result.code ===0){ //成功
            getMsgList(dispatch,result.data._id)  //登录成功则获取消息列表，并建立socket.io
            dispatch(auth_success(result.data))
        }
        else{
            dispatch(error_msg(result.msg))
        }
    }
}


export const login =(user) =>{
    const {username} = user
    if(!username)
        return error_msg('用户名不能为空')  //表单前端验证

    return async dispatch =>{
        const response =await reqLogin(user)
        const result = response.data
        if(result.code ===0){ //成功
            getMsgList(dispatch,result.data._id)
            dispatch(auth_success(result.data))
        }
        else{
            dispatch(error_msg(result.msg))
        }
    }
}

export const updateUser = (user) =>{
    //
    return async dispatch =>{
        const response = await reqUpdateUser(user)
        const result = response.data
        if(result.code ===0 ){
            dispatch(receive_user(result.data))
        }
        else{
            dispatch(reset_user(result.msg))
        }
    }
}

export const getUser = () =>{
    return async dispatch =>{
        const response = await reqUser();
        const result = response.data
        if(result.code === 1) {
            dispatch(reset_user(result.msg))
        } 
        else{
            getMsgList(dispatch,result.data._id)
            dispatch(receive_user(result.data))
        }
    }
}

export const getUserList = (type) =>{
    return async dispatch =>{
        const response = await reqUserList(type)
        const result = response.data;
        if(result.code ===0){
            dispatch(receive_userlist(result.data))}
    }
    
}

//发送消息，通过socket.io

export const sendMsg = ({from,to,content}) =>{
    return dispatch =>{
        io.socket.emit('sendMsg',{from,to,content})
    }
}

export const readMsg = (targetID,userid) =>{
    return async dispatch =>{
        const response = await reqReadMsg(targetID)
        const result =response.data
        if(result.code===0){
            const count = result.data
            const from =targetID
            const to = userid
            dispatch(read_msg(count,from,to))
        }
    }
}

//获取消息列表数据，只是一个函数不不代表分发action
async function getMsgList(dispatch,userid){
    intiIO(dispatch,userid);
    const response = await reqChatMsgList()
    const result =response.data
    if(result.code ===0){
        const {users,chatMsgs} = result.data
       dispatch( receive_msg_list({users,chatMsgs,userid}))

    }
}


