import React, {Component} from 'react'
import {connect} from 'react-redux'
import {register} from '../../redux/acitons'
import {Card, WingBlank, WhiteSpace} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import QueueAnim from 'rc-queue-anim'

import  PropTypes from 'prop-types'
import  '../../assets/css/inde.less'
const Header = Card.Header
const Body = Card.Body


class UserList extends Component {
  static propTypes ={
    userList:PropTypes.array.isRequired
  }
   render() {
     const {userList} = this.props
    return (
    <WingBlank style={{marginBottom: 53,marginTop:48}}>
      <QueueAnim>
        {      userList.map((user) => (
        <div key={user._id}>
        <WhiteSpace/>
        <Card onClick={() => {this.props.history.push(`/chat/${user._id}`);console.log('pressed')}}>
          <Header thumb={require(`../../assets/headers/${user.header}.png`)} extra={user.username} />
          <Body>
            <div>职位: {user.post}  </div>
            {user.company?<div>公司: {user.company}  </div>:null}
            {user.salary?<div>月薪: {user.salary}  </div>:null}
            <div>描述: {user.info}  </div>
          </Body>
        </Card>
    </div>
      )) }
      </QueueAnim>

    </WingBlank>
    )
    }
}


export default withRouter (UserList)



