//选择头像的UI组件
import React, {Component} from 'react'
import {List, Grid} from 'antd-mobile'
import PropTypes from 'prop-types'
import '../../assets/css/inde.less'
export default class HeaderSelector extends Component {
  static propTypes = {
    setHeader: PropTypes.func.isRequired
  }
  state = {
    icon: null
  }
  constructor(props) {
    super(props)
    this.headerlist = [];
    for (let i = 1; i < 21; i++) {
      this
        .headerlist
        .push({
          icon: require(`../../assets/headers/头像${i}.png`),
          text: `头像${i}`
        })
    }
  }
  handleClick = ({icon, text}) => {
    this.setState({icon})
    this
      .props
      .setHeader(text)
  }
  render() {
    const {icon} = this.state;
    const title = !icon
      ? (
        <div className='header-origin'>请选择头像</div>
      )
      : (
        <div className='header-div'>
          已选择头像:
          <img src={icon} className='header_img'/>
        </div>
      );

    return (
      <List renderHeader={() => title}>
        <Grid
          data={this.headerlist}
          columnNum={5}
          onClick={val => {
          this.handleClick(val)
        }}></Grid>
      </List>
    )
  }
}


