import {combineReducers} from  'redux'
import {AUTH_SUCCESS,ERROR_MSG,RECIEVE_USER,RESET_USER,RECIEVE_USERLIST,RECIEVE_MSG_LIST,RECIEVE_MSG,READ_MSG}  from './aciton-types'
import {getRedirectTo} from '../utils/index'

const initUser ={
    username:'',
    type:'',
    msg:'',//存放错误信息
    redirectTo: ''  //自动重定向路径
}
function user(state=initUser,aciton){
    const {type,data} = aciton;
    switch (type) {
        case AUTH_SUCCESS:
            return {...data,redirectTo:getRedirectTo(data.type,data.header)}
        case ERROR_MSG:
            return {...state,msg:data}
        case RECIEVE_USER:
            return data
        case RESET_USER:
            return {...initUser,msg:data}
        default:
            return state;
    }
}
const initUserList=[]
function userlist(state=initUserList,aciton){
    console.log(aciton)
    switch(aciton.type){
        case RECIEVE_USERLIST:
            return aciton.data
        default:
            return state
    }
}

const initChat={
    users:{},
    chatMsgs:[],
    unReadCount:0  //总的未读数量
}
function chat(state=initChat,action){
    switch (action.type){
        case RECIEVE_MSG_LIST:
            const {users,chatMsgs}=action.data
            return {users,chatMsgs,
            unReadCount:chatMsgs.reduce((pre,msg)=> pre+(!msg.read&&msg.to===action.data.userid?1:0),0)}
        case RECIEVE_MSG:
            const {chatMsg,userid} = action.data
            return {users:state.users,chatMsgs:[...state.chatMsgs,chatMsg],
                unReadCount:state.unReadCount+(!chatMsg.read&&chatMsg.to===userid?1:0)}
        case READ_MSG:
            const {count,from,to} =action.data
            return {users:state.users,
                chatMsgs:state.chatMsgs.map(msg =>{
                    if(msg.from==from &&msg.to ==to){
                        return {...msg,read:true}
                    }
                    else{
                        return msg
                    }
                }),
                unReadCount:state.unReadCount-count}
        default:             //这不能少，不然chat状态无法初始化
            return state    //不能initstate，不然随便dipatch一个aciton就会重置状态
    }
}

export default combineReducers (
    {
        user,
        userlist,
        chat
    }
)






