import React, {Component} from 'react'
import {connect} from 'react-redux'
import {reset_user} from '../../redux/acitons'
import {Redirect} from 'react-router-dom'
import {Result, List, WhiteSpace, Button, Modal} from 'antd-mobile'
import Cookies from 'js-cookie'
const Item = List.Item
const Brief = Item.Brief

class Personal extends Component {
  Logout = () =>{
    Modal.alert('退出登录','确认退出登录吗',[
      {text:'取消',},//对象里面可以写一个回调函数
      {text:'确认',onPress: () =>{
        //删除cookie
        Cookies.remove('userid')
        //删除redux中state，改变状态要分发aciton
        this.props.reset_user()
      }}
    ])
  }
  render() {
    const {user} =this.props
    const {username,info,header,company,salary,post} = user
    return (
      <div style={{marginBottom: 53,marginTop:48}}>
        <Result
        img={<img src={require(`../../assets/headers/${header}.png`)} style={{width: 50}}
        alt="header"/>}
        title={username}
        message={company}
        />
      <List renderHeader={() => '相关信息'}>
        <Item multipleLine>
          {}
          <Brief>职位: {post} </Brief>
          <Brief>简介: {info} </Brief>
          {salary?<Brief>薪资: {salary} </Brief>:null}
        </Item>
      </List>
      <WhiteSpace/>
      <List>
        <Button type='warning' onClick={this.Logout} >退出登录</Button>
      </List>
      </div>
      )
  }
}

export default connect(state =>({user:state.user}),
    {reset_user}
)(Personal)


