import React, {Component} from 'react'
import {connect} from 'react-redux'
import {register} from '../../redux/acitons'
import {Redirect} from 'react-router-dom'
import UserList from '../../components/userlist/userlist'
import '../../assets/css/inde.less'
import {sendMsg,readMsg} from '../../redux/acitons.js'
import {List, NavBar, InputItem,Grid,Icon} from 'antd-mobile'
import QueueAnim from 'rc-queue-anim'
const Item = List.Item
const Brief = Item.Brief

class Chat extends Component {
    state ={
        content:'',
        isShow:false  //显示表情列表
    }

    componentDidMount() {
        // 初始显示列表
        window.scrollTo(0, document.body.scrollHeight)
        //点进来具体聊天界面后，要清清除chatid未读信息
        const from = this.props.user._id
        const to = this.props.match.params.userid  //
        this.props.readMsg(to,from)
      }
    
      componentDidUpdate () {
        // 更新显示列表
        window.scrollTo(0, document.body.scrollHeight)
      }

    componentWillMount () {
        // 初始化表情列表数据
        const emojis = ['😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀'
          ,'😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣'
          ,'😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣'
          ,'😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣']
        this.emojis = emojis.map(emoji => ({text: emoji}))   //因为antd要求，grid.data数组里面的每个元素不能是字符串（表情就是字符串），要是对象，所以这么做，返回一个
      }
    componentWillUnmount(){
        const from = this.props.user._id
        const to = this.props.match.params.userid  //
        this.props.readMsg(to,from)
    }


      toggleShow = () => {
        const isShow = !this.state.isShow
        this.setState({isShow})
        if(isShow) {
          // 异步手动派发resize事件,解决表情列表显示的bug
          setTimeout(() => {
            window.dispatchEvent(new Event('resize'))
          }, 0)
        }
      }


    handleSend = () =>{
        const from = this.props.user._id
        const to = this.props.match.params.userid  //
        const content = this.state.content.trim()
        const chatid = [from,to].sort()
        if(content){
            this.props.sendMsg({from,to,content})
        }
        this.setState({content:'',isShow:false})
    }
  render() {
    const from = this.props.user._id
    const{users,chatMsgs} =this.props.chat
    if(!users[from]) return null
    const to = this.props.match.params.userid  //
    const chatid = [from,to].sort().join('_')

      
      const Msgs = chatMsgs.filter(msg => msg.chat_id ===chatid)
      const target = users[to].header
      
      const icon =require(`../../assets/headers/${target}.png`)
    return (
      <div id='chat-page'>
        <NavBar onLeftClick={()=> this.props.history.goBack()} className='mainNav' icon={<Icon type='left'/>}>{users[to].username}</NavBar>
        <List style={{marginTop:50,marginBottom:50}}>
            <QueueAnim >            
                {Msgs.map(msg =>{
                if(from ===msg.to){  //别人发给我的
                    return <Item wrap  key={msg._id} thumb={icon}>{msg.content}</Item>
                }
            else return <Item wrap key={msg._id} className='chat-me' extra='我'>{msg.content}</Item>
            })}
            </QueueAnim>

        </List> 
        <div className='am-tab-bar'>
          <InputItem 
            value={this.state.content} 
            placeholder="请输入" 
            onFocus={() => this.setState({isShow: false})}
            onChange={(val) => this.setState({content:val})} 
            extra={              
            <span>
                <span onClick={this.toggleShow} style={{marginRight:3}}>😊</span>
                <span onClick={this.handleSend}>发送</span>
              </span>}/>
              {this.state.isShow ? (
            <Grid
              data={this.emojis}
              columnNum={8}
              carouselMaxRow={4}
              isCarousel={true}
              onClick={(item) => {
                this.setState({content: this.state.content + item.text})
              }}
            />
          ) : null}
        </div>
      </div>
    )
  }
}

export default connect(state => ({user: state.user,chat:state.chat}), {sendMsg,readMsg})(Chat)


