import React, {Component} from 'react'
import {connect} from 'react-redux'
import {register} from '../../redux/acitons'
import {Redirect} from 'react-router-dom'
import UserList from '../../components/userlist/userlist'
import  '../../assets/css/inde.less'
import  {getUserList} from '../../redux/acitons.js'
class Boss extends Component {
  componentDidMount(){
    this.props.getUserList('qiuzhizhe')
  }
  render() {
    return (<UserList  userList={this.props.userlist} />)
  }
}

export default connect(state =>({userlist:state.userlist,}),
    {getUserList}
)(Boss)


