/*
提示找不到页面的UI路由组件
 */

import React from "react"
import {Button} from "antd-mobile"
import {TabBar,} from 'antd-mobile'
import PropTypes from 'prop-types'
import {withRouter} from 'react-router-dom'
const Item = TabBar.Item

class NavFooter extends React.Component {
    static propTypes ={
        navList:PropTypes.array.isRequired,
        unReadCount:PropTypes.number.isRequired
    }
  render() {
      const {navList,unReadCount} =this.props
      const newnavList =navList.filter(nav =>(!nav.hidden ))
      const path =this.props.location.pathname //由于navfooter并不是一个路由组件，所以读取不到location，解决方案就是使用withRoute()
    return (
      <TabBar>
          {
              newnavList.map((nav) =>(
              <Item
              key={nav.path} title={nav.text} 
              badge={nav.path==='/message'?unReadCount:0}
              icon={{uri:require(`./images/${nav.icon}.png`) }}
              selectedIcon ={{uri:require(`./images/${nav.icon}-selected.png`) }}
              selected= {path === nav.path}
              onPress ={() => this.props.history.replace(nav.path)}
              ></Item>))
          } 
          
      </TabBar>
    )
  }
}

export default withRouter(NavFooter)


