import axios from 'axios'

export default function ajax(url = '', data = {}, type = 'GET') {
    if (type === 'GET') {
    // 准备 url query 参数数据
    // 发送 get 请求
    return axios.get(url,{params:data})
    } else {
    // 发送 post 请求
    return axios.post(url, data) // data: 包含请求体数据的对象
}
}


