import React, {Component} from 'react'
import {NavBar,WingBlank,List,InputItem,WhiteSpace,Radio,Button} from  'antd-mobile'
import {connect} from 'react-redux'
import {register} from '../../redux/acitons'
import  Logo from '../../components/logo/logo'
import ListItem from 'antd-mobile/lib/list/ListItem'
import {Redirect} from 'react-router-dom'

import  '../../assets/css/inde.less'
 class Register extends Component {
    state ={
        username:'',
        password:'',
        password2:'',
        type:''
    }
    handleChange = (type,val) =>{
        this.setState(
            {
                [type]:val
            }
        )
    }
    register = () => {
        this.props.register(this.state)
    }
    toLogin =() =>{
        this.props.history.replace('/login')
    }
  render() {
      const {msg,redirectTo} = this.props.user
      const {type,} = this.state;
      //如果有值，就要重定向
      //为什么把redirecTo放进state里，因为这样才能在redirectTo改变时，更新状态，而更新状态才能引起组件的重绘！
      //某个因素的改变，组件要重绘，就应该放进state。
      //在这里，虽然是改变路径，但是采用的是重绘组件的方式，重绘一个Redirect组件，改变路径
      if(redirectTo){
          return <Redirect to={redirectTo}></Redirect>
      }
    return (
      <div>
          <NavBar>招&nbsp;聘</NavBar>
          <Logo />
          <WingBlank>
              <List>
                  {msg ? <div className='error-msg'>{msg} </div>:null}
                  <InputItem placeholder='请输入用户名' onChange={(val) =>this.handleChange('username',val)} >用户名：</InputItem>
                  <WhiteSpace/>
                  <InputItem placeholder='请输入密码' type='password' onChange={(val) =>this.handleChange('password',val)}>密&nbsp;&nbsp;&nbsp;码:</InputItem>
                  <InputItem placeholder='请输入确认密码' type='password' onChange={(val) =>this.handleChange('password2',val)} >确认密码:</InputItem>
                  <ListItem>
                      <span>用户类型</span>&nbsp;&nbsp;&nbsp;
                      <Radio checked={type ==='qiuzhizhe'} onChange={() => this.handleChange('type','qiuzhizhe')}>求职者</Radio>&nbsp;&nbsp;&nbsp;
                      <Radio checked={type ==='laoban'} onChange={() => this.handleChange('type','laoban')}>老板</Radio>
                  </ListItem>
                  <WhiteSpace/>
                  <Button type='primary' onClick={this.register}>注&nbsp;&nbsp;&nbsp;册</Button>
                  <WhiteSpace/>
                  <Button onClick={this.toLogin}>已有账户</Button>
              </List>
          </WingBlank>
     </div>
    )
  }
}

export default connect(state =>({user:state.user}),
    {register}
)(Register)


