import React,{Component} from 'react'
import ReactDOM from 'react-dom'
import {connect} from 'react-redux'
import {NavBar,} from 'antd-mobile'
import {HashRouter, Route, Switch, Redirect} from 'react-router-dom'
import  Cookies from 'js-cookie'
import {Provider} from 'react-redux';
import {getUser} from '../../redux/acitons'

import BossInfo from '../boss-info/boss-info'
import WorkInfo from '../worker-info/worker-info'
import NotFound from '../../components/not-found/not-found'
import Message from '../message/message'
import Boss from '../boss/boss'
import Worker from '../worker/worker'
import  Personal from '../personal/personal'
import  Chat from '../chat/chat'
import  NavFooter from '../../components/nav-footer/nav-footer'

import {getRedirectTo} from '../../utils/index'
import  '../../assets/css/inde.less'

class Main extends Component {
      // 给组件对象添加属性
  navList = [ // 包含所有导航组件的相关信息数据
    {
      path: '/boss', // 路由路径
      component: Boss,
      title: '求职者列表',
      icon: 'dashen',
      text: '求职者',
    },
    {
      path: '/worker', // 路由路径
      component: Worker,
      title: '老板列表',
      icon: 'laoban',
      text: '老板',
    },
    {
      path: '/message', // 路由路径
      component: Message,
      title: '消息列表',
      icon: 'message',
      text: '消息',
    },
    {
      path: '/personal', // 路由路径
      component: Personal,
      title: '用户中心',
      icon: 'personal',
      text: '个人',
    }
  ]



    componentDidMount(){
        const cookieid = Cookies.get('userid')
        const {_id} = this.props.user
        if(cookieid && !_id ){
            this.props.getUser()
        }
    }
  render() {
      //先要检查用户是否登录，不然直接url输入/bossinfo就渲染出了对应的页面
      //若用户为登录，则不要渲染/bossinfo，重定向到登录界面
      //当浏览器输入url时，去找对应url的组件，在路由组件内将其拦截下来！
      const {user} = this.props
      console.log('未读消息',this.props.unReadCount)
      const {_id} = user             //获取redux中的userid
      const cookie_id = Cookies.get('userid')  //获取cookie中的userid
      if(!cookie_id){ //cookie中没有记录，重新登录
          return <Redirect to='/login' />
      }
      else{
          if(!_id){  //cookie中有记录，但redux中没有用户信息（可能由于刷新页面，或手动点击退出），要等后台数据回来，改变redux后再渲染
              return null
          }
          else{    //cookie,redux都有记录，
              const path = this.props.location.pathname
              if(path ==='/'){
                  const path =getRedirectTo(user.type,user.header)
                  return <Redirect to={path}/>
              }
          }
      }
      const {navList} = this
      const path = this.props.location.pathname
      const curNav = navList.find(nav => path == nav.path)
      if(user.type ==='laoban'){
          navList[1].hidden =true
          navList[0].hidden =false
      }
      else{
        navList[1].hidden =false
        navList[0].hidden =true
      }
    return (
      <div>
          {curNav?(<NavBar className='mainNav' >{curNav.title}</NavBar>):null}
          <Switch>
              {navList.map((nav => <Route key={nav.path} path={nav.path} component={nav.component}></Route>))};
              {/* 回调函数nav=> {},nav=> ()两个写法不一样，前者要写return! */}
              <Route path='/bossinfo' component={BossInfo}></Route>
              <Route path='/workerinfo' component={WorkInfo}></Route>
              <Route path='/chat/:userid' component={Chat}></Route>
              <Route  component={NotFound}></Route>
          </Switch>    
          {curNav?(<NavFooter navList ={navList} unReadCount={this.props.unReadCount} ></NavFooter>):null}
      </div>
    )
  }
}
export default connect(
    state =>({user:state.user,unReadCount:state.chat.unReadCount}),
    {getUser}
)(Main)


